import React, { useState } from 'react';
import { DropdownList, NumberPicker, Combobox, DatePicker } from 'react-widgets';
import './SearchForm.css';

function SearchForm({ onSearch }) {
  const [formData, setFormData] = useState({
    type: 'any',
    listingType: 'any',
    minPrice: null,
    maxPrice: null,
    minBedrooms: null,
    maxBedrooms: null,
    dateFrom: null,
    dateTo: null,
    postcode: ''
  });

  const propertyTypes = ['any', 'house', 'flat'];
  const listingTypes = ['any', 'sale', 'rent'];
  const postcodeAreas = ['BR5', 'BR6', 'SW19', 'E14', 'BR1', 'NW1'];

  const handleSubmit = (e) => {
    e.preventDefault();
    onSearch(formData);
  };

  const handleReset = () => {
    setFormData({
      type: 'any',
      listingType: 'any',
      minPrice: null,
      maxPrice: null,
      minBedrooms: null,
      maxBedrooms: null,
      dateFrom: null,
      dateTo: null,
      postcode: ''
    });
  };

  return (
    <form className="search-form" onSubmit={handleSubmit}>
      <div className="form-group">
        <label>Property Type</label>
        <DropdownList
          data={propertyTypes}
          value={formData.type}
          onChange={(value) => setFormData({ ...formData, type: value })}
        />
      </div>

      <div className="form-group">
        <label>Listing Type</label>
        <DropdownList
          data={listingTypes}
          value={formData.listingType}
          onChange={(value) => setFormData({ ...formData, listingType: value })}
        />
      </div>

      <div className="form-row">
        <div className="form-group">
          <label>Min Price ()</label>
          <NumberPicker
            value={formData.minPrice}
            onChange={(value) => setFormData({ ...formData, minPrice: value })}
            min={0}
          />
        </div>

        <div className="form-group">
          <label>Max Price ()</label>
          <NumberPicker
            value={formData.maxPrice}
            onChange={(value) => setFormData({ ...formData, maxPrice: value })}
            min={0}
          />
        </div>
      </div>

      <div className="form-row">
        <div className="form-group">
          <label>Min Bedrooms</label>
          <NumberPicker
            value={formData.minBedrooms}
            onChange={(value) => setFormData({ ...formData, minBedrooms: value })}
            min={0}
            max={10}
          />
        </div>

        <div className="form-group">
          <label>Max Bedrooms</label>
          <NumberPicker
            value={formData.maxBedrooms}
            onChange={(value) => setFormData({ ...formData, maxBedrooms: value })}
            min={0}
            max={10}
          />
        </div>
      </div>

      <div className="form-row">
        <div className="form-group">
          <label>Date Added From</label>
          <DatePicker
            value={formData.dateFrom}
            onChange={(value) => setFormData({ ...formData, dateFrom: value })}
            max={new Date()}
          />
        </div>

        <div className="form-group">
          <label>Date Added To</label>
          <DatePicker
            value={formData.dateTo}
            onChange={(value) => setFormData({ ...formData, dateTo: value })}
            max={new Date()}
          />
        </div>
      </div>

      <div className="form-group">
        <label>Postcode Area</label>
        <Combobox
          data={postcodeAreas}
          value={formData.postcode}
          onChange={(value) =>
            setFormData({
              ...formData,
              postcode: (value || '').toString().toUpperCase().trim()
            })
          }
          allowCreate
          placeholder="e.g. BR1, NW1"
          className="postcode-input"
        />
        <small>Enter first part of postcode (e.g. BR1, NW1)</small>
      </div>

      <div className="form-actions">
        <button type="submit" className="btn-search">
          Search Properties
        </button>

        <button
          type="button"
          className="btn-reset"
          data-testid="reset-button"
          onClick={handleReset}
        >
          Reset
        </button>
      </div>
    </form>
  );
}

export default SearchForm;
